<template>
	<div class="theme-dashboard-view">
		<h1>Help</h1>

		<div class="my-3">
			<h4>Frequently Asked Questions</h4>
			<div
				class="my-3"
				v-for="(qa, index) in questionAnswers"
				:key="index"
				@click="toggleAnswer(index)"
			>
				<div
					class="
						bg-light
						p-2
						rounded
						d-flex
						justify-content-between
						qa-hover
					"
				>
					<strong>{{ qa.question }}</strong>
					<span v-show="openAnswers.includes(index)">
						<i class="fa fa-chevron-down" aria-hidden="true"></i>
					</span>
					<span v-show="!openAnswers.includes(index)">
						<i class="fa fa-chevron-right" aria-hidden="true"></i>
					</span>
				</div>
				<div
					class="border p-3"
					style="min-height: 90px"
					v-show="openAnswers.includes(index)"
				>
					{{ qa.answer }}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "Help",
	data() {
		return {
			// TODO: Pull question and answers from a json file
			questionAnswers: [
				{
					question: "How do i add a product?",
					answer: "Look to the sidebar, there is a button that says create product",
				},
				{
					question: "How do i add a product?",
					answer: "Look to the sidebar, there is a button that says create product",
				},
				{
					question: "How do i add a product?",
					answer: "Look to the sidebar, there is a button that says create product",
				},
			],
			openAnswers: [],
		};
	},
	methods: {
		toggleAnswer(index) {
			if (this.openAnswers.includes(index)) {
				this.openAnswers = this.openAnswers.filter(
					(answer) => answer != index
				);
			} else {
				this.openAnswers.push(index);
			}
		},
	},
};
</script>

<style scoped>
.qa-hover {
	cursor: pointer;
}
.qa-hover:hover {
	background: #aaa !important;
}
</style>